/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.eltdf-content .woocommerce {
    @include eltdfRelativeHolderLayout();
}

.eltdf-pl-main-holder {
    margin: 0 0 22px;
    
    .eltdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

.eltdf-woo-pl-info-on-image-hover{
    ul.products{
        > .product{

            .eltdf-pl-text-inner{
                padding-top: 15%;
            }

            a{
                margin: 0;
            }
        }
    }
}

.eltdf-woo-pl-info-below-image{
    ul.products{
        > .product{
            .eltdf-pl-text {
                
                .eltdf-pl-text-inner{
                    position: relative;
                    margin-bottom: 26px;
                    overflow: hidden;
                }

                .price{
                    opacity: 1;
                    @include eltdfTransition(opacity .1s, transform .3s);
                }

                a{
                    @include eltdfAbsoluteHolderLayout();
                    border: 0;
                    opacity: 0;
                    padding: 19px 0;
                    font-family: $default-heading-font;
                    font-weight: 500;
                    font-size: 17px;
                    display: block;
                    margin: 0;
                    line-height: 1;
                    color: $first-main-color;
                    background-color: transparent !important;
                    @include eltdfTransition(.3s);
                    @include eltdfTransform(translateX(20%));

                    &.add_to_cart_button:after{
                        font-family: 'ElegantIcons';
                        content: "\e013";
                        line-height: 0;
                        margin-left: 3px;
                        font-size: 14px;
                        display: inline-block;
                        @include eltdfTransition(.4s);
                        backface-visibility: hidden;
                    }

                    &:hover{
                        color: $first-main-color;
                        background-color: transparent;

                        &:after {
                            @include eltdfTransform(translateY(-3px));
                        }
                    }
                }
            }

            &:hover{
                .eltdf-pl-text {

                    .price{
                        opacity: 0;
                        @include eltdfTransform(translateX(-20%));
                    }

                    a {
                        opacity: 1;
                        @include eltdfTransform(translateX(0));
                    }
                }
            }
        }
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;

        &.outofstock{

            img{
                opacity: .5;
            }
        }

        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .eltdf-pl-text {
                opacity: 1 !important;
            }

            .eltdf-pl-text a.added {
                opacity: 0 !important;
            }
        }
        
        .eltdf-product-list-title {
            margin: 15px 0 0;
        }
        
        .eltdf-pl-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 0;
        }
        
        .price {
            @include eltdfWooCommercePriceStyle();
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;
    
            .eltdf-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }
        
        .eltdf-pl-inner {
            @include eltdfRelativeHolderLayout();
            overflow: hidden;
            background-color: #fcfcfc;
            
            .eltdf-pl-image {
                @include eltdfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }

            .eltdf-pl-text {
                @include eltdfAbsoluteHolderLayout();
                padding: 10px;
                opacity: 0;
                background-color: rgba($default-heading-color, 0.75);
                box-sizing: border-box;
                @include eltdfTransition(opacity .2s ease-out);
            }
            
            .eltdf-pl-text-outer {
                @include eltdfTableLayout();
                text-align: center;
            }
            
            .eltdf-pl-text-inner {
                @include eltdfTableCellLayout();
                
                .eltdf-product-list-title,
                .star-rating {
                    color: #fff;
                }

            }
            
            > a {
                @include eltdfAbsoluteHolderLayout();
            }
        }
        
        .eltdf-pl-text-wrapper {
            @include eltdfRelativeHolderLayout();
            text-align: center;
            background-color: #fcfcfc;
        }
    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.eltdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .eltdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */