/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.eltdf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonSize(medium);
        @include eltdfButtonSolidColor();
        background-color: $first-main-color !important;
        padding: 7px 18px;
        cursor: pointer;
        z-index: 3;

        &:hover {
            background-color: $header-dark-color !important;
        }
    }

    a.added_to_cart {

        &:after{
            font-family: 'ElegantIcons';
            content: "\e013";
            line-height: 0;
            margin-left: 3px;
            font-size: 14px;
            display: inline-block;
            @include eltdfTransition(0.2s ease-out);
            backface-visibility: hidden;
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */