/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 80px;
    $inner-padding: 15px; // distance between image and content
    $widget-list-item-padding: 15px; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include eltdfH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 7px;
            margin-top: 7px;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        span { // currency symbol
            color: inherit;
        }
    }

    del,
    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 20px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #e5e5e5;
                height: 5px;
                position: relative;

                .ui-slider-handle {
                    background-color: $first-main-color;
                    border-radius: 50%;
                    cursor: pointer;
                    height: 11px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -3px;
                    width: 11px;
                    z-index: 2;

                    &:last-child {
                        @include eltdfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: $first-main-color;
                    display: block;
                    height: 5px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 30px 0 0;

            .button {
                float: right;
                padding: 0;
                font-family: $default-heading-font;
                font-weight: 500;
                font-size: 17px;
                display: block;
                margin: 0;
                line-height: 1;
                color: inherit;
                background-color: transparent !important;
                text-decoration: underline;

                &:hover {
                    color: $first-main-color;
                    background-color: transparent !important;
                }
            }

            .price_label {
                float: left;
                line-height: 17px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                &:before,
                &:after {
                    content: " "; /* 1 */
                    display: table; /* 2 */
                }

                &:after {
                    clear: both;
                }

                & {
                    *zoom: 1;
                }

                a {

                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: 0;
                        width: 100%;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                        font-size: 15px;
                        font-weight: 400;
                        font-family: $default-heading-font;
                        letter-spacing: 0.015em;
                    }
                }

                > *:not(a) {
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }

                > ins {
                    margin: 0;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart{
        li{
            padding-top: 15px;
        }

        a {
            margin: 0 0 0 #{$thumb + $inner-padding};
            display: block;
            padding: 0;

            @include eltdfH5();
            font-size: 15px;
        }
    }

    &.widget_recent_reviews {
        a {
            padding: 0 !important;
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            top: 0;
            text-decoration: none;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include eltdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include eltdfButtonDefaultStyle();
                @include eltdfButtonSize();
                @include eltdfButtonSolidColor();
                line-height: 1em;
                padding: 12px 17px;

                &:hover {
                    @include eltdfButtonSolidHoverColor();
                    text-decoration: none;

                }

                &:first-child {
                    float: left;
                }

                &:last-child {
                    float: right;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */