/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 32px;
    width: auto;
    height: 41px;
    margin: 0;
    padding: 0 30px;
    color: #fff;
    line-height: 41px;
    letter-spacing: .015em;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include eltdfTransform(translateZ(0));
    font-family: $default-heading-font;
    font-weight: 400;
    font-size: 14px;
}

@mixin eltdfWooCommerceSaleStyle() {
    right: 0;
    background-color: $first-main-color;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    left: 0;
    background-color: $default-heading-color;
}

@mixin eltdfWooCommerceNewProductStyle() {
    right: 0;
    background-color: $first-main-color;
}


/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */


/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    font-family: $default-heading-font;
    font-weight: 500;
    font-size: 17px;
    position: relative;
    display: block;
    padding: 19px 0;
    line-height: 1;
    color: $first-main-color;
    * {
        font-size: inherit;
        line-height: inherit;
    }
    del {
        color: #cfcfcf;
    }
    ins {
        text-decoration: none;
        color: $first-main-color;
    }
}


/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */


/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 11px;
    line-height: 1.1em;
    letter-spacing: 3px;
    color: #e1e1e1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    &:before {
        content: "\e033\e033\e033\e033\e033";
    }
    span {
        color: #ffdb00;
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        &:before {
            content: "\e033\e033\e033\e033\e033";
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    &:before {
        content: "\e033";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: -3px;
        bottom: 6px;
        display: block;
        color: #ffdb00;
        @extend %star-ratings;
        z-index: -1;
        letter-spacing: 7px;
    }
}


/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */