/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 190px;
							width: calc(100% - 190px);
						}

                        @include laptop-landscape {
                            &:first-child {
                                left: 162px;
                                width: calc(100% - 162px);
                            }
                        }
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: calc(25% - 20px);
								height: calc(33.33% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (33.33 * $i)#{'%'};
									$second_param: (7.5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0;
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 59%;
		    margin: 0;
		    padding: 0 22px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 41%;
        padding: 0 0 0 5px;
        float: left;
        box-sizing: border-box;

	    .eltdf-single-product-title {
			line-height: 50px;
		    display: block;
			width: 70%;
			float: left;
			text-align: left;
			margin: 0 0 5px;
	    }

		.eltdf-woo-social-share-holder {
			@include eltdfRelativeHolderLayout();
			line-height: 50px;
			width: 30%;
			float: right;
			text-align: right;

			> * {
				display: table-cell;
				vertical-align: top;
			}

			> span {
				padding: 0 9px 0 0;
			}

			.eltdf-list {
				display: inline-block;
			}
		}
	
	    .price {
		    @include eltdfWooCommercePriceStyle();
			clear: both;
			margin: 7px 0 0;
			padding-bottom: 15px;
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 0 0 3px;
			clear: both;

			.star-rating{
				font-size: 15px;
                > span {
                    max-width: 99%;
                }
			}

			.woocommerce-review-link{
				display: none;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 32px 0 0;
		
		    > span {
			    display: block;
			    margin: 3px 0;
				font-family: $default-heading-font;
				font-weight: 400;
				color: $default-heading-color;

				> span{
					font-family: $default-text-font;
					color: $default-text-color;
				}
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
		    margin: 35px 0 0;
	    }
	
	    .eltdf-quantity-buttons {
		    margin: 0 20px 0 0;
	    }
    }
	
	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: 30px 0 0;
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-bottom: 2px dotted $default-border-color;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-family: $default-heading-font;
				font-size: 17px;
				font-weight: 600;
				padding: 13px 15px;
				overflow: hidden;
				@include eltdfTransition(all .2s ease);

				&:first-child{
					padding-left: 0;
				}

				&:last-child{
					padding-right: 0;

				}

				&:before {
					content: "\24";
					position: absolute;
					top: 50%;
					left: -25px;
					width: 20px;
					font-family: 'ElegantIcons';
					vertical-align: unset;
					color: $first-main-color;
					font-size: 18px;
					line-height: inherit;
					font-weight: 800;
					font-style: normal;
					text-rendering: auto;
					-webkit-font-smoothing: antialiased;
					-moz-osx-font-smoothing: grayscale;
					@include eltdfTransition(left .2s ease-in-out);
					@include eltdfTransform(translateY(-50%));
				}

				&:hover, &.active {

					padding-left: 25px;

					&:before {
						left: 0;
					}
				}

				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 0;
				}
			}
		}
		
		.entry-content {
			@include eltdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			p {
				margin: 18px 0 20px;
			}
		}
		
		table {
			margin: 18px 0 20px;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				font-weight: 400;
				font-family: $default-heading-font;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 20px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 18px 0 20px;
			
			h2 {
				font-size: 18px;
				line-height: 24px;
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;

                    .star-rating {
                        > span {
                            max-width: 99%;
                        }
                    }

					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				display: block;
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}

				form{
					> .comment-form-rating{
						margin-top: 9px;

						> p{
							margin-bottom: 10px;
						}
					}
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include eltdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 25px 0 0;

		> h1, > h2, > h3, > h4 {
			display: block;
			margin: 20px 0 30px;
			font-size: 20px;
			line-height: 1.4em;
		}
		
		.product {
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */