/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 10px;
    color: #231f20;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon{
                font-size: 20px;
                line-height: 20px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }

            .eltdf-cart-number {
                font-size: 9px;
                color: #fff;
                background-color: $first-main-color;
                top: 10px;
                left: 12px;
                text-align: center;
                line-height: 15px;
                height: 15px;
                width: 15px;
                position: absolute;
                border-radius: 50%;
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            opacity: 1;
            visibility: visible;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 37px);
    left: auto;
    right: 0;
    width: 300px;
    background-color: #f9f9f9;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include eltdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 38px 30px;
        padding: 0;

        li {
            @include eltdfTableLayout();
            padding: 5px 0 5px;
            box-sizing: border-box;

            &.eltdf-empty-cart {
                color: initial;
                border-bottom: 0;
                font-weight: 400;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
                border-top: 2px dotted $default-border-color;
            }

            &.eltdf-empty-cart{
                border-top: 0 !important;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 80px;
        vertical-align: top;
        padding-top: 5px;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-left: 13px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            font-size: 15px;
            font-family: $default-text-font;
            font-weight: 400;

            a:hover{
                text-decoration: underline;
                color: $additional-heading-color;
            }
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 18px;
	        line-height: 1;
            color: #c1c1c1;

            &:hover{
                color: $first-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity {
            display: block;
            margin: 3px 0 0;
        }

        .amount {
            display: block;
            margin: 3px 0 0;
            color: $first-main-color;
            font-weight: 400;
        }
    }

    .eltdf-cart-bottom {
        padding: 11px 0 0;
        margin-top: 14px;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 20px;
            font-weight: 400;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .eltdf-total-amount {
                text-align: right;
                color: $first-main-color;
            }
        }

        .eltdf-view-cart, .eltdf-view-checkout {
            @include eltdfRelativeHolderLayout();
            width: 48%;
            height: 40px;
            line-height: 40px;
            font-family: $default-heading-font;
            font-size: 13px;
            font-weight: 600;
            color: #fff;
            text-align: center;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }
        }

        .eltdf-view-cart{
            background-color: $default-heading-color;
            float: left;

            &:hover {
                color: #fff;
                background-color: $first-main-color;
            }
        }

        .eltdf-view-checkout{
            background-color: $first-main-color;
            float: right;

            &:hover {
                color: #fff;
                background-color: $default-heading-color;
            }
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */